		},
		onLoad(option) {
			var that = this;
			that.getAll();
			that.spfl();
			that.proAll();
			setTimeout(function(){
				console.log(that.prorow)
			},2000)
		},
		methods: {
			
			getAll() {
				var that = this;
				let param = {};
				
				this.$http.get('/index/carousel', param, {
					custom: {
						show: false
					}
				}).then((res) => {
					that.lbt=res
				})
			},
			spfl(){
				var that = this;
				let param = {};
				
				this.$http.get('/index/cats', param, {
					custom: {
						show: false
					}
				}).then((res) => {
					that.spfls=res
				})
			},
			proAll() {
				this.loading = true;
				var that = this;
				let param = {};
				
				this.$http.get('/items/catItems', param, {
				}).then((res) => {
					that.prorow=res
				})
			},
			decrease() {
				 if (this.quantity > 1) {
				            this.quantity--;
				}
			}
		},

	}
</script>

